import Permission from '../../../permission';

export default class UserWistikiServicePermission extends Permission {
  constructor(acl) {
    super(acl, '/users/:email/wistikis/:__feathersId', ['GET', 'PUT']);
  }

  build() {
    this.allow('user', ['GET', 'PUT']);
    this.allow('application', ['GET', 'PUT']);
  }
}
